  --color-address-g: rgba(255, 255, 255, .6);
  --color-socials-bg: rgba(255, 255, 255, .1);
  --color-backdrop: rgba(0, 0, 0, .2);
  --font-roboto: "Roboto", sans-serif;
  --font-raleway: "Raleway", sans-serif;
  --border-portfolio: 1px solid #ececec;
