.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: rgba(0, 0, 0, 0);
  box-shadow: inset 0 0 250px 1px rgba(255, 255, 255, .7), inset 1px 30px 500px 20px rgba(0, 0, 250, .37);
}

.is-hidden:not(.not-hidden) {
  transition: opacity 700ms var(--cubic-primary) 0ms, visibility 750ms var(--cubic-primary) 0ms, box-shadow 250ms var(--cubic-primary) 0ms, background-color 250ms var(--cubic-primary) 250ms;
